@import "../node_modules/normalize.css/normalize.css";
* {
    box-sizing: border-box; }

html {
    height: 100%; }

body {
    height: 100%; }

#root {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100%; }

.container {
    display: flex;
    flex-grow: 1;
    padding: 0 15px; }



.select {
    width: 100%; }

ul {
    margin: 0;
    padding: 0; }

li {
    list-style: none; }
